.write-div-styl{
width:100%;
height:100vh;
display:flex;
flex-direction: column;
justify-content:center;
align-items:center;
background: #F8F8FF;
}
.write-div-main{
    /* box-shadow: 3px 3px 3px solid black; */
    width:34rem;
    height:500px;
    display:grid;
    grid-template-rows: 1fr 5fr;
    border: 3px solid orange; 
    
}

.write-div-head{
    width:100%;
    border-bottom: 2px solid black;
    background: whitesmoke;
    display:flex;
    justify-content: flex-start;
    align-items: center;
}

.write-div-head:hover{
    border-bottom: 2px solid orange;
}
.write-text{
    resize: none;
    width: 100%;
    height:100%;
    border: none;
    outline: none;
    padding: 20px;
    caret-color: orange;
    font-size: large

}
.write-text:hover{
    box-shadow: 0 0 10px 2px orange;

}
.btn-write{
    margin:5px;
    padding:5px;
    height:40px;
    width: 40px;
    cursor: pointer;
    border: 1px solid grey;
    transition: transform .2s ease-in ;

}
.btn-write:hover{
    transform: scale(1.2) ;
    border: 3px solid orange;
}
.btn-div{
    margin:8px;   
}
 .post-btn{
    margin:20px;
    width:100px;
    height:50px;
    padding:20px;
    border:1px solid orange;
    border-radius: 10px ;
    cursor:pointer;
}
.post-btn:hover{
    color:#F8F8FF;
    background-color: orange;
}
.btn-div-post{
    width:100%;
    display: flex;
    justify-content: flex-start;

}